.fb {
    display: none;
    &:hover path {
        fill: $active-color;
    }
    &--about path {
        fill: $grey;
    }
    &--foot {
        display: block;
        margin-top: 21px;
    }
}

@media (min-width: 482px) {
    .fb {
        display: block;
        margin-left: 7px;
        &--foot {
            margin: 0 18px 0 0;
        }
    }
}
